.App {
	display: flex;
	justify-content: center;
	align-items: center;
	min-width: 100vw;
	min-height: 100vh;
	padding: 10px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

.innerContainer {
	min-width: 90vw;
	min-height: 90vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

h1 {
	font-size: 3rem;
	margin-bottom: 1rem;
}

input {
	width: 100%;
	height: 3rem;
	font-size: 2rem;
	text-align: center;
	border: none;
	border-bottom: 1px solid black;
	outline: none;
}

h2 {
	font-size: 2rem;
	margin-top: 1rem;
	font-weight: 300;
	text-align: left;
	width: 100%;
}

p {
	font-size: 1.5rem;
	margin-top: 1rem;
	font-weight: 300;
	text-align: left;
	width: 100%;
}

button {
	width: 60%;
	height: 3rem;
	font-size: 1.5rem;
	margin-top: 1rem;
	border: none;
	outline: none;
	background-color: #00b894;
	color: white;
	cursor: pointer;
}

.buttonContainer {
	display: flex;
	justify-content: space-around;
	width: 100%;
	gap: 10px;
}
